:root {
    --side-panel-with : 25vw;
    --top-bar-height : 50px;
}

#side-panel {
    width: var(--side-panel-with);
    height: calc(100% - calc(var(--top-bar-height) + 50px));
    margin: 5px;
    position: relative;
    float: left;
    overflow-y: scroll;
}

#renderer-panel {
    position: relative;
    float: right;
    margin: 5px;
    width: calc(100% - calc(var(--side-panel-with) + 30px));
    height: calc(100% - calc(var(--top-bar-height) + 50px));
    background: white;
}

#top-bar {
    height: var(--top-bar-height);
    padding-top: 35px;
    padding-left: 5px;
}

.list-button {
    display: block;
    margin-top: 5px;
    margin-left: 5px;
}